<template>
  <div class="container-learn" ref="Learn">
    <div class="container-learn-up" :style="{width:MoveWidth ,height:MoveHeight}"></div>
    <div class="container-learn-down" :style="{width:MoveWidth ,height:MoveHeight}"></div>
    <div class="container-learn-out" :style="{opacity:MoveOpacity}"></div>
    <div class="container-learn-word">{{Text}}</div>
  </div>
</template>

<script>
export default {
data() {
  return {
    MoveWidth:`80%`,
    MoveHeight:`0%`,
    MoveOpacity:0,
    CliHeight:0,
    CliWidth:0
  }
},
methods: {
   learnScroll(){
     if(this.CliWidth<=1242){
     if(this.$refs.Learn.getBoundingClientRect().top >100 && this.$refs.Learn.getBoundingClientRect().top <(this.CliHeight+750)){
   this.MoveWidth = `100%`;
  this.MoveHeight = `100%`;
  this.MoveOpacity = 1
 }
   }else{
     if(this.$refs.Learn.getBoundingClientRect().top >100 && this.$refs.Learn.getBoundingClientRect().top <(this.CliHeight-200)){
  this.MoveWidth = `100%`;
  this.MoveHeight = `100%`;
  this.MoveOpacity = 1
 }
   }
    },
},
mounted() {
  this.CliHeight = window.screen.height;
  this.CliWidth = window.screen.width;
  this.learnScroll();
  document.addEventListener('scroll',this.learnScroll)
},
beforeDestroy() {
  document.removeEventListener('scroll',this.learnScroll)
},
props:{
  Text:{
    type:String,
    required:true
  }
}
}
</script>

<style scoped lang="less">
@media(min-device-width:1025px){
  .container-learn{
    width: 3.5rem;
    height: 1rem;
    position: relative;
    background: white;
    transform: skew(-14deg);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    .container-learn-up{
      width: 80%;
      height: 0;
      position: absolute;
      top: 0;
      left: 0;
      border-top: 0.05rem solid black;
      border-right: 0.05rem solid black;
      box-sizing: border-box;
      transition: width 0.1s linear, height 0.3s linear 0.1s;
    }
    &:hover .container-learn-up{
      visibility: hidden;
    }
    .container-learn-down{
      width: 80%;
      height: 0;
      position: absolute;
      bottom: 0;
      right: 0;
      border-bottom: 0.05rem solid black;
      border-left: 0.05rem solid black;
      box-sizing: border-box;
      transition: width 0.1s linear, height 0.3s linear 0.1s;
    }
    &:hover .container-learn-down{
      visibility: hidden;
    }
    .container-learn-word{
      padding-right: 0.5rem;
      font-weight: bold;
      font-size: 0.35rem;
      position: relative;
      color: white;
      &::after{
        content: "";
        width: 0.15rem;
        height: 0.15rem;
        border-top: 0.05rem solid white;
        border-right: 0.05rem solid white;
        position: absolute;
        right: 0.1rem;
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
      }
    }
    &:hover .container-learn-word{
    color: black;
    }
    &:hover .container-learn-word::after{
      border-top: 0.05rem solid black;
      border-right: 0.05rem solid black;
    }
    .container-learn-out{
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background: black;
      opacity: 0;
      transition: opacity 0.3s ease-out 0.4s , background 0.3s linear;
    }
    &:hover .container-learn-out{
      background: white;
    }
  }
}
@media(max-device-width:1024px){
  .container-learn{
    width: 3.5rem;
    height: 1rem;
    position: relative;
    background: white;
    transform: skew(-14deg);
    display: flex;
