        border-right: 0.05rem solid white;
        position: absolute;
        right: 0.1rem;
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
      }
    }
    .container-learn-out{
      width: 100%;
      height: 100%;
      position: absolute;
